#orientation{
    margin-bottom: calc($spacing-xxl * 2);
    h4{
        margin: 0 auto $spacing-xs;
        z-index: 1;
    }
    // h3{
    //     display: none;
    // }
    .content{
        display: grid;
        gap: $spacing;
    }
    border-radius: $spacing;
    padding: $spacing $spacing $spacing-lg $spacing;
    box-shadow: 0 4px 6px #0000001f, 0 5px 15px #00000023;
    position: relative;
    background: white;
    &::before{
        content: '';
        position: absolute;
        bottom: -$spacing-xs;
        right: -$spacing-xs;
        height: 50px;
        width: 70px;
        border-radius: 4px 4px $spacing-md 4px;
        background: var(--green-apple);
        box-shadow: 0 4px 6px #0000001f, 0 5px 15px #00000023;
        z-index: -1;
    }
    &::after{
        content: '';
        position: absolute;
        top: -$spacing-xs;
        left: -$spacing-xs;
        height: 50px;
        width: 70px;
        border-radius: $spacing-md 4px 4px 4px;
        background: var(--canary);
        box-shadow: 0 4px 6px #0000001f, 0 5px 15px #00000023;
        z-index: -1;
    }
}